// Êü•ËØ¢ÊñáÁ´†ÂàóË°®
export const getArticleList = data => {
  return request({
    method: 'post',
    url: '/article/getArticleList',
    params: {
      keyword: data.keyword
    },
    data: {
      ...data.pageParams
    }
  })
}

